<template>
  <FormKit
    v-model="data"
    type="form"
    :actions="false"
  >
    <FormKitSchema
      v-if="orderSchema"
      :schema="orderSchema"
    />
    <Button
      label="Добавить"
      @click="emits('submit', data)"
    />
  </FormKit>
</template>

<script setup lang="ts">
import {TransactionInPlnEntry} from '@/services/transaction.service';
const emits = defineEmits(['submit']);

type NullablePartial<T> = {
  [P in keyof T]: T[P] | null;
};

const data = ref({
  userDisplayId: null,
  amount: null,
  comment: '',
  orderId: null,
} as NullablePartial<TransactionInPlnEntry>);

const orderSchema = ref([
  {
    $formkit: 'primeInputNumber',
    name: 'userDisplayId',
    label: 'ID пользователя',
    validation: 'required',
  },
  {
    $formkit: 'primeInputNumber',
    name: 'amount',
    label: 'Изменение баланса',
    suffix: ' PLN',
    minFractionDigits: 2,
    maxFractionDigits: 2,
    validation: 'required|min:-1000000000|max:1000000000',
  },
  {
    $formkit: 'primeInputNumber',
    name: 'orderId',
    label: 'ID заказа',
  },
  {
    $formkit: 'primeInputText',
    name: 'comment',
    label: 'Коментарий',
  },
]);
</script>
